import './CatalogProduct.scss';
import React from 'react'
import {Card, Descriptions} from 'antd';


const CatalogProduct = (props) => {
    return (
        <Card
            style={{marginBottom: 16}}
            type="inner"
            title={props.name}
            size={'small'}
        >
            <Descriptions bordered column={1} size={'small'}>
                <Descriptions.Item span={2} label="">{props.aricle}</Descriptions.Item>
                <Descriptions.Item span={2} label="price">{props.price}</Descriptions.Item>
                <Descriptions.Item span={2} label="  ">{props.special_price}</Descriptions.Item>
                <Descriptions.Item span={2} label="">{props.count}</Descriptions.Item>
                <Descriptions.Item span={2} label=". ">{props.measurement}</Descriptions.Item>
                <Descriptions.Item span={2} label=" ">{props.united_pruchases}</Descriptions.Item>
                <Descriptions.Item span={2} label="  ">{props.show_main? '': ''}</Descriptions.Item>
                <Descriptions.Item span={2} label="">{props.description}</Descriptions.Item>
                <Descriptions.Item span={2} label="1 ">{props.category}</Descriptions.Item>
                <Descriptions.Item span={2} label="2 ">{props.parent_category}</Descriptions.Item>
                <Descriptions.Item span={2} label="3 ">{props.grand_category}</Descriptions.Item>

            </Descriptions>

        </Card>
    )
}

export default CatalogProduct;
